export default ".weather-widget{max-width:230px;width:100%;display:inline-flex;position:relative}.weather-widget__settings{position:absolute;z-index:1;background-color:#fff;transform:translateX(-230px);opacity:0;transition:all .3s linear}.weather-widget__settings_show{transform:translateX(0);opacity:1}.slide-fade-enter-active,.slide-fade-leave-active{transition:all .3s linear}.slide-fade-enter-from,.slide-fade-leave-to{transform:translateX(20px);opacity:0}"